.donate {
	position: relative;
	background: url(/assets/images/donate-bg.jpg) no-repeat 50% 50% / cover;
	&:before {
		content: "";
		display: block;
		position: absolute;
		top:0;
		left: 0;
		width: 100%;
		height: 100%;
		background: linear-gradient(to right, rgba($white,1) 0%,rgba(36,43,69,0) 100%);
		z-index: 1;
	}
	.u-content {
		max-width: 760px;
		font-size: 16px;
		line-height: 30px;
	}
	.section--content {
		display: flex;
		align-items: flex-start;
		flex-wrap: wrap;
	}
	&--content {
		flex: 1;
		min-width: 280px;
	}
	&--side {
		flex-shrink: 0;
		width: 360px;
		margin-left: 40px;
		h3 {
			margin-bottom: 20px;
			font-size: 20px;
			line-height: 20px;
			font-weight: 700;
			text-transform: uppercase;
		}
		strong {
			font-weight: 700;
		}
	}
	.box {
		margin-bottom: 40px;
		padding: 20px;
		border-radius: 10px;
		box-shadow: 0 0 20px rgba(0,0,0,.05);
		background: $white;
		img {
			display: block;
			margin-bottom: 20px;
		}
		p {
			margin-bottom: 20px;
			&:last-child {
				margin-bottom: 0;
			}
		}
		a {
			color: $green;
			text-decoration: underline;
			font-weight: 700;
			&:hover {
				color: $blue;
			}
		}
	}
	@media (max-width: 768px) {
		&--side {
			width: 100%;
			margin: 40px 0 0;
		}
	}
}
.donation-form {
	display: flex;
	align-items: flex-start;
	.input {
		position: relative;
		margin-right: 20px;
		span {
			position: absolute;
			top: 50px;
			right: 20px;
			display: block;
			font-size: 20px;
			line-height: 20px;
			font-weight: 700;
		}
		label {
			display: block;
			margin-bottom: 10px;
			font-size: 16px;
			line-height: 20px;
			font-weight: 700;
		}
		input {
			display: block;
			appearance: none;
			padding: 13px 43px 13px 13px;
			height: 60px;
			width: 200px;
			appearance: none;
			border: 2px solid #E0E0E0;
			border-radius: 6px;
			box-shadow: 0 1px 4px rgba(0,0,0,.1);
			background: $white;
		}
	}
	.btn {
		padding: 20px;
		margin-top: 30px;
	}
	@media (max-width: 480px) {
		.input {
			input {
				width: 120px;
			}
		}
	}
}