.u-content {
  font-size: 14px;
  line-height: 25px;
  .intro {
    font-size: 24px;
    line-height: 30px;
    margin-bottom: 20px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  h1 {
    font-size: 50px;
    line-height: 50px;
    text-transform: uppercase;
    margin-bottom: 20px;
  }
  h3,
  h4,
  h5,
  h6 {
    font-weight: 700;
    margin-bottom: 20px;
  }
  h2 {
    font-weight: 300;
    font-size: 40px;
    line-height: 40px;
    margin-bottom: 40px;
    text-transform: uppercase;
  }
  h3 {
    font-size: 20px;
    line-height: 30px;
    text-transform: uppercase;
    margin-top: 20px;
  }
  h4 {
    font-size: 18px;
    line-height: 25px;
    text-transform: uppercase;
    margin-bottom: 20px;
  }
  h5 {
    font-size: 14px;
    line-height: 25px;
    margin-bottom: 20px;
  }
  h6 {
    font-size: 12px;
    line-height: 20px;
  }
  a:not(.btn) {
    text-decoration: underline;
    color: $text-color;
    &:hover {
      color: $green;
    }
    &:not([href]) {
      color: $text-color;
      text-decoration: none;
    }
  }
  p,
  ul,
  ol,
  blockquote,
  img {
    margin-bottom: 20px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  img {
    max-width: 100%;
  }
  em {
    font-style: italic;
  }
  b,
  strong {
    font-weight: bold;
  }
  small {
    font-size: 14px;
    line-height: 20px;
  }
  ul {
    list-style: none;
    li {
      position: relative;
      padding-left: 25px;
      margin-top: 10px;
      &:first-child {
        margin-top: 0;
      }
      &:before {
        content: "";
        position: absolute;
        display: block;
        left:6px;
        top: 10px;
        width: 6px;
        height: 6px;
        background: $text-color;
        border-radius: 50%;
      }
    }
  }
  ol {
    list-style: none;
    counter-reset: count;
    li {
      position: relative;
      padding-left: 25px;
      margin-top: 10px;
      counter-increment: count;
      &:before {
        content: counter(count) ".";
        position: absolute;
        display: block;
        left:6px;
        top: 0;
      }
    }
  }
  .links {
    li {
      display: flex;
      align-items: center;
      padding: 0;
      i {
        display: block;
        width: 20px;
        margin-right: 10px;
        text-align: center;
        font-size: 20px;
        color: $green;
      }
      a {
        font-weight: 700;
        text-decoration: none;
        span {
          text-decoration: underline;
          margin-right: 10px;
        }
        small {
          font-weight: 400;
          font-size: 12px;
          text-decoration: none;
        }
      }
      &:before {
        display: none;
      }
    }
  }
  sup {
    vertical-align: super;
    font-size: 14px;
    line-height: 20px;
  }
  sub {
    vertical-align: sub;
    font-size: 14px;
    line-height: 20px;
  }
  figure {
    margin: 40px 0;
    &:first-child {
      margin-top: 0;
    }
    &:last-child {
      margin-bottom: 0;
    }
    img {
      display: block;
      margin: 0;
    }
    figcaption {
      padding: 20px;
      display: block;
      font-size: 14px;
      line-height: 20px;
      box-shadow: inset 0 -1px 0 $black;
    }
  }
  blockquote {
    margin: 40px 0;
    font-size: 20px;
    line-height: 30px;
    width: 340px;
    float: right;
    small {
      display: block;
      text-align: right;
    }
  }
  .align-center {
    margin: 40px auto;
  }
  .align-right {
    float: right;
    margin: 40px 0 40px 40px;
  }
  .align-left {
    float: left;
    margin: 40px 40px 40px 0;
  }
  .text-align-center {
    text-align:center;
  }
  .text-align-left {
    text-align: left;
  }
  .text-align-right {
    text-align: right;
  }
  .text-align-justify {
    text-align: justify;
  }
  table {
    width: 100%;
    font-size: 14px;
    line-height: 20px;
    margin-bottom: 20px;
    thead {
      th,
      td {
        color: $white;
        background: $black;
        text-align: left;
        padding: 10px;
        font-weight: bold;
        border-bottom: 3px solid $black;
      }
    }
    tfoot,
    tbody {
      th {
        font-weight: bold;
        text-align: left;
      }
      td,
      th {
        border-bottom: 1px solid $black;
        padding: 10px;
      }
      tr:nth-child(even) {
        background: #FAFAFA;
      }
    }
  }
  iframe {
    margin-bottom: 40px;
    display: block;
  }
  .table-wrapper {
    overflow: auto;
  }
  .video-wrapper {
    position: relative;
    padding-bottom: 56.25%;
    height: 0;
    margin-bottom: 40px;
    max-width: 980px;
    video,
    iframe,
    object,
    embed {
      position: absolute;
      top: 0;
      left: 0;
      width: 100% !important; // Overwrite inline styles
      height: 100% !important; // Overwrite inline styles
      margin: 0;
      max-width: none;
    }
  }
  pre {
    margin: 20px 0;
  }
  &--split {
    display: flex;
    align-items: flex-start;
    flex-wrap: wrap;
    margin: 0 -20px 0;
  }
  &--block {
    flex:1;
    margin: 0 20px 40px;
    min-width: 280px;
  }
  a.video-teaser {
    position: relative;
    display: flex;
    align-items: center;
    color: $text-color;
    font-size: 11px;
    line-height: 15px;
    text-decoration: none;
    &:hover {
      color: $text-color;
      .text {
        .title {
          &:after {
            background-image: url('data:image/svg+xml,#{$icon-play-tiny-dark-fill}');
          }
        }
      }
    }
    img {
      display: block;
      width: 120px;
      margin: 0 20px 0 0;
    }
    .text {
      .title {
        font-family: $oswald;
        font-size: 16px;
        line-height: 20px;
        text-transform: uppercase;
        margin: 0;
        span {
          margin-right: 5px;
        }
        &:after {
          content: ".";
          display: inline-block;
          vertical-align: top;
          height: 20px;
          width: 20px;
          background: url('data:image/svg+xml,#{$icon-play-tiny-dark}') no-repeat 50% 50%;
          font-size: 0.1px;
          transition: all .2s;
        }
      }
    }
  }
  @media (max-width: 1023px) {
    line-height: 20px;
    .intro {
      font-size: 18px;
      line-height: 20px;
    }
    h1 {
      font-size: 40px;
      line-height: 40px;
    }
    h2 {
      font-size: 30px;
      line-height: 30px;
      margin-bottom: 20px;
    }
    h3 {
      font-size: 20px;
      line-height: 30px;
    }
    blockquote {
      margin: 20px 0;
      font-size: 16px;
      line-height: 25px;
      width: auto;
      float: none;
    }
  }
  @media (max-width: 480px) {
    .intro {
      font-size: 16px;
    }
    h1 {
      font-size: 30px;
      line-height: 30px;
    }
    h2 {
      font-size: 24px;
      line-height: 30px;
    }
  }
}