.materials {
	position: relative;
	background: url(/assets/images/materials-bg.jpg) no-repeat 50% 50% / cover;
	&:before {
		content: "";
		display: block;
		position: absolute;
		top:0;
		left: 0;
		width: 100%;
		height: 100%;
		background: linear-gradient(to right, rgba($white,1) 0%,rgba(36,43,69,0) 100%);
		z-index: 1;
	}
	&--list {
		display: flex;
		align-items: flex-start;
		flex-wrap: wrap;
		margin: 0 -10px -40px;
	}
	&--item {
		flex:1;
		font-size: 14px;
		line-height: 20px;
		min-width: 280px;
		margin: 0 10px 40px;
		p {
			margin-bottom: 20px;
		}
	}
}