.tabs {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  &--link {
    display: block;
    position: relative;
    transition: all 200ms ease;
    margin: 10px 0 0;
    padding: 10px 20px;
    font-family: $oswald;
    font-size: 14px;
    color: $text-color;
    text-align: center;
    text-transform: uppercase;
    border-radius: 4px 4px 0 0;
    z-index: 3;
    &:hover {
      color: $green;
    }
    &.u-active {
      z-index: 3;
      color: #fff;
      background: $text-color;
    }
  }
  &--item {
    position: relative;
    z-index: 2;
    display: none;
    width: 100%;
    border-top: 2px solid #242B45;
    background: $white;
    &.u-open {
      display: block;
    }
  }
}